.main-nav {
  position: relative;
  width: auto;
}
.main-nav__toggle {
  position: absolute;
  display: block;
  width: 24px;
  height: 24px;
  cursor: pointer;
  border: none;
  background-color: #ffffff;
  top: -55px;
  left: 300px;
}
.main-nav--nojs .main-nav__toggle {
  display: none;
}
.main-nav--opened .main-nav__toggle::before,
.main-nav--opened .main-nav__toggle::after {
  content: "";
  position: absolute;
  top: 10px;
  right: 30px;
  width: 24px;
  height: 2px;
  background-color: #000000;
}
.main-nav--opened .main-nav__toggle::before {
  transform: rotate(45deg);
}
.main-nav--opened .main-nav__toggle::after {
  transform: rotate(-45deg);
}
.main-nav--closed .main-nav__toggle::before {
  content: "";
  position: absolute;
  top: 0;
  right: 30px;
  width: 24px;
  height: 2px;
  background-color: #000000;
  box-shadow: 0 8px 0 0 #000000, 0 16px 0 0 #000000;
}
.main-nav__wrapper {
  position: absolute;
  z-index: 2;
  width: 100%;
  padding-top: 22px;
  padding-bottom: 24px;
  background-color: #ffffff;
}
@media (min-width: 768px) {
  .main-nav__wrapper {
    position: static;
  }
}
@media (min-width: 1400px) {
  .main-nav__wrapper {
    background-color: rgba(0, 0, 0, 0);
  }
}
@media (max-width: 767px) {
  .main-nav--closed .main-nav__wrapper {
    display: none;
  }
}
.main-nav--nojs .main-nav__wrapper {
  position: static;
}
.main-nav__list {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  text-align: center;
